html,
body {
	height: 100%; }

body {
	min-width: 320px;
	background: $black;
	@include f('light', 20, 22); }

.out {
	overflow: hidden;
	min-height: 100%; }

.inner {
	position: relative;
	z-index: 10;
	margin: 0 auto;
	max-width: 980px;
	@include clr;
	@include lg {
		max-width: 700px; }
	@include md {
		max-width: 500px; } }

//typography
h1, h2, h3, h4, h5, h6, strong, b, cite, th, i, em {
	font-weight: normal;
	font-style: normal; }

a {
	color: inherit;
	text-decoration: none; }

.btn {
	display: inline-block;
	vertical-align: top;
	padding: 7px 20px 2px;
	color: $white;
	background: $black;
	@include f('mreg', 23, 40);
	text-align: center;
	transition: background 0.25s linear;
	&:hover {
		background: lighten($black, 20%); }
	@include lg {
		padding: 19px 20px 13px;
		width: 100%;
		@include f(null, 48, 60); }
	@include sm {
		padding: 19px 30px 13px;
		width: auto;
		@include f(null, 24, 30); }
	&_yellow {
		span {
			@include lg {
				color: $yellow; } } }
	span {
		@include lg {
			display: block; } } }
